::selection {
  background-color: rgba(0, 0, 0, 0.15); }

* {
  transition: color 280ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, background-color 280ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 280ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1) 0ms; }

*:before,
*:after {
  transition: inherit; }

svg {
  fill: currentColor;
  transition: fill 280ms cubic-bezier(0.4, 0, 0.2, 1) 0ms; }

body {
  min-width: 22.25rem;
  font-family: "Roboto Condensed", sans-serif;
  cursor: default;
  background-color: black; }

a,
img,
input {
  outline: none; }

header {
  padding: 0 5%;
  /* Some custom media queries below to properly show/hide menu bar stuff. Using tachyons size classes left the menu's layout broken on certain widths*/
  /* Menu icon stuff from https://www.w3schools.com/howto/howto_css_menu_icon.asp*/
  /* Rotate first bar */
  /* Fade out the second bar */
  /* Rotate last bar */ }
  header .header-items {
    transition: height 0.3s ease;
    padding: 0.6rem;
    align-items: center; }
  @media screen and (max-width: 47.5rem) {
    header .hidden {
      position: absolute;
      top: -9999px;
      opacity: 0; } }
  header .expand {
    height: 9rem; }
  @media screen and (min-width: 32rem) {
    header .expand {
      height: 7.8rem; } }
  @media screen and (min-width: 47.5rem) {
    header .expand {
      height: 3rem; } }
  header .collapse {
    transition: opacity ease-in 0.1s; }
  @media screen and (max-width: 32rem) {
    header .SearchCategory {
      margin: 1rem 0; } }
  header .menu-icon {
    cursor: pointer;
    margin-left: 2rem; }
  @media screen and (min-width: 47.5rem) {
    header .menu-icon {
      display: none; } }
  header .bar1, header .bar2, header .bar3 {
    width: 35px;
    height: 5px;
    background-color: #2196F3;
    margin: 6px 0;
    transition: 0.4s; }
  header .change .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-9px, 6px);
    background-color: #FFC107; }
  header .change .bar2 {
    transition: 0s;
    opacity: 0; }
  header .change .bar3 {
    -webkit-transform: rotate(45deg) translate(-8px, -8px);
    transform: rotate(45deg) translate(-8px, -8px);
    background-color: #FFC107; }
  header #title:hover span:nth-child(2) {
    color: #FFC107; }
  header #title:hover span:nth-child(3) {
    color: currentColor; }

.SearchCategory .radio-box input,
.SearchCategory .radio-box label {
  cursor: pointer; }

.SearchCategory .radio-box input {
  visibility: hidden;
  margin: 0 -0.05rem 0 0.6rem; }

.SearchCategory .radio-box input::before,
.SearchCategory .radio-box input:checked::before,
.SearchCategory .radio-box input::after,
.SearchCategory .radio-box .input:checked::after {
  content: "";
  visibility: visible;
  box-sizing: border-box;
  position: absolute;
  border-radius: 50%; }

.SearchCategory .radio-box input::before {
  width: 0.5rem;
  height: 0.5rem;
  margin: 0.1875rem;
  background-color: transparent; }

.SearchCategory .radio-box input:checked::before {
  background-color: #2196F3; }

.SearchCategory .radio-box input::after {
  height: 0.875rem;
  width: 0.875rem;
  border: 0.125rem solid #FFC107; }

.SearchCategory .radio-box input:checked::after {
  border-color: #FFFFFF; }

#searchbox {
  margin: 0 0.6rem 0 0; }
  #searchbox input {
    width: 11rem;
    padding: 0.3rem; }
  #searchbox input::selection {
    background: rgba(255, 255, 255, 0.15); }
  #searchbox input::-moz-selection {
    background: rgba(255, 255, 255, 0.15); }
  #searchbox input::-webkit-input-placeholder {
    color: #9E9E9E;
    opacity: 1; }
  #searchbox input::placeholder {
    color: #9E9E9E;
    opacity: 1; }
  #searchbox input:focus::-ms-input-placeholder {
    color: #9E9E9E; }
  #searchbox input:focus:-ms-input-placeholder {
    color: #9E9E9E; }
  #searchbox input:focus,
  #searchbox input:not(:placeholder-shown) {
    color: #000000;
    border-color: #2196F3; }

